@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DynaPuff:wght@600&display=swap);
:root{
  --clr-background: #f0f0f0;
  --clr-purple: #8909ff;
  --clr-purple-dark: #9903c7;
  --clr-black:#000;
  --clr-white:#fff;
  --clr-tag:#8809ff89;
  --nav-height:6rem;
  --padding-side:15rem;

}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins',sans-serif;
}
html{
  font-size: 62.5%;
  scroll-behavior: smooth;
}
#root{
  overflow-x: hidden;
}

@media (max-width:1024px) and (min-width:861px) {
  :root{
    --padding-side:10rem;
  }
}
@media (max-width:860px) and (min-width:769px){
  :root{
    --padding-side:5rem;
  }
}
@media (max-width:768px){
  :root{
    --padding-side:5rem;
  }
}
.carouselContainer{
    height: 70vh;
    width: 100%;
    border-radius: 1rem;
    position: relative;
    overflow: hidden;
}
.carouselContainer .btn{
    position: absolute;
    z-index: 1;
    outline: none;
    border: none;
    border-radius: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 4rem;
    height: 100%;
    width: 2rem;
    top: 0;
    color: white;
    text-shadow: 1px 2px 3px #000,-1px 0px 3px #000;
    background: transparent;
}
.carouselContainer .btn:hover{
    background: rgba(0, 0, 0, 0.5);
}
.carouselContainer .leftBtn{
    left: 0;
}
.carouselContainer .rightBtn{
    right: 0;
}
.carouselDot{
    position: absolute;
    left: 50%;
    bottom: 1rem;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    height: 50px;
    width: 200px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    z-index: 1;
    grid-gap: .5rem;
    gap: .5rem;
    justify-content: center;
    align-items: center;
}
.carouselDot .dot{
    height: .8rem;
    width: .8rem;
    border-radius: 50%;
    background: var(--clr-purple);
    border: .1px solid var(--clr-white);
    transition: .5s all ease;
}
.carouselDot .dotActive{
    width: 2.4rem;
    border-radius: 2.4rem;
}
.carouselImages{
    width: 100vw;
    height: 100%;
    position: relative;
}
.carouselImages .img{
    position: absolute;
    top: 0;
    left: 0;
    height: 70vh;
    opacity: 0;
    transition: .5s all ease;
}
.carouselImages .imgActive{
    opacity: 1;
}

@media (max-width:1024px) and (min-width:861px) {
    :root{
      --padding-side:10rem;
    }
  }
  @media (max-width:860px) and (min-width:769px){
    :root{
      --padding-side:5rem;
    }
  }
  @media (max-width:768px){
    .carouselContainer{
        height: 40vh;
    }
    .carouselImages .img{
        height: 40vh;
    }
  }
.skillSec{
    padding: var(--nav-height)  var(--padding-side) 0;
    min-height: 100vh;
    width: 100vw;
    background: var(--clr-background);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}
.skillSec h3{
    font-size: 3rem;
    font-family: 'Poppins',sans-serif;
    position: relative;
}
.skillSec h3::after{
    content: "";
    position: absolute;
    bottom: 0%;
    left: 0;
    top: unset;
    width: 100%;
    height: 0.5rem;
    border-radius: 0.2rem;
    background-color: var(--clr-purple);
}
.skillSec p{
    margin-top: 1rem;
    font-size: 2rem;
    font-weight: 500;
    line-height: 1.5;
    color: var(--clr-purple);
    font-family: 'Poppins',sans-serif;
}
.skillList{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-gap: 1rem;
    gap: 1rem;
    margin-top: 2.5rem;
}
.skillItem{
    background: var(--clr-purple);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 1rem 2rem;
    grid-gap: .5rem;
    gap: .5rem;
    border-radius: .5rem;
    cursor: pointer;
    overflow: hidden;
    position: relative;
    transition: .5s transform ease;
}
.skillItem::before,.skillItem span{
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    left: 0;
    top: 0;
    background: var(--clr-white);
    transition: .5s all ease;
}
.skillItem span{
    top: unset;
    left: unset;
    bottom: 0;
    right: 0;
}

.skillItem p{
    color: var(--clr-white);
    transition: .5s color ease;
}
.skillItem:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.skillItem:hover::before,.skillItem:hover span{
    width: 100%;
    height: 100%;
    border-radius: .5rem;
}
.skillItem:hover img{
    -webkit-filter: invert(.3) sepia(1) saturate(97) hue-rotate(277deg);
            filter: invert(.3) sepia(1) saturate(97) hue-rotate(277deg);
}
.skillItem:hover p{
    color: var(--clr-purple);
    z-index: 1;
}

@media (max-width:1024px) and (min-width:769px){
    .skillList{
        grid-template-columns: repeat(3,1fr);
    }
  }

@media (max-width:768px){
    .skillList{
        grid-template-columns: repeat(2,1fr);
        margin-bottom: 2rem;
    }
}
#root{
    position: relative;
}
.homeSec{
    padding: var(--nav-height) var(--padding-side) 0 var(--padding-side);
    min-height: 100vh;
    width: 100vw;
    background: var(--clr-purple) url(/static/media/homeBack.15f80155.png);
    background-position: bottom;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
}
.textDiv,.imgDiv{
    height: 100%;
    width: 60%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.helloDiv{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.helloDiv h3{
    font-size: 3rem;
    font-family: 'Poppins',sans-serif;
    position: relative;
}
.helloDiv h3::after{
    content: "";
    position: absolute;
    top: 50%;
    left: calc(100% + .5rem);
    width: 100%;
    height: .5rem;
    border-radius: .2rem;
    background-color: var(--clr-purple);
}
.textDiv h1{
    font-size: 6rem;
    line-height: 1;
    color: var(--clr-purple);
    font-family: 'Poppins',sans-serif;
}
.textDiv p{
    font-size: 2rem;
    line-height: 1.5;
    color: var(--clr-black);
    font-family: 'Poppins',sans-serif;
}
.btnDiv{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin-top: 3rem;
}
.btn{
    text-decoration: none;
    font-size: 1.8rem;
    padding: 1rem 3rem;
    border-radius: .8rem;
    color: var(--clr-black);
    overflow: hidden;
    transition: .5s all ease;
}

.btnSecondary,.btnPrimary{
    color: var(--clr-purple);
    border: 2px solid var(--clr-purple);
    background: transparent;
    position: relative;
    z-index: 1;
}
.btnPrimary{
    background:transparent;
    color: var(--clr-white);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    grid-gap: 0.5rem;
    gap: 0.5rem;
}
.btnPrimary svg{
    height: 30px;
}
.btnPrimary .btnArrow{
    fill: var(--clr-white);
    transition: .5s fill ease;
}
.btnPrimary:hover .btnArrow{
    fill: var(--clr-purple);
}
.btnSecondary::before,.btnPrimary::before{
    content: "";
    position: absolute;
    top: -3px;
    left:0;
    bottom: -3px;
    width: 0;
    z-index: -1;
    background-color: var(--clr-purple);
    transition: .5s width ease;
}
.btnPrimary::before{
    width: 100%;
    background:linear-gradient(to right, var(--clr-purple), var(--clr-purple-dark));
}
.btnPrimary:hover{
    color: var(--clr-purple);
}
.btnPrimary:hover::before{
    width: 0%;
    left: unset;
    right: 0;
}
.btnSecondary:hover{
    color: var(--clr-white);
}
.btnSecondary:hover::before{
    width: 100%;
    left: unset;
    right: 0;
}
.imgDiv{
    align-items: flex-start;
}
.imgDiv img{
    width: 100%;
}
.aboutSec{
    margin-top: -2px;
    background: var(--clr-background);
}
.aboutSec .imgDiv img{
    width: 90%;
}
.aboutSec .helloDiv h3::after{
    content: "";
    position: absolute;
    bottom: 0%;
    left: 0;
    top: unset;
    width: 100%;
    height: .5rem;
    border-radius: .2rem;
    background-color: var(--clr-purple);
}
.aboutSec .textDiv p{
    margin-top: 1rem;
    font-size: 2rem;
    line-height: 1.5;
    color: var(--clr-black);
    font-family: 'Poppins',sans-serif;
}

.skillSec{
    background: var(--clr-background);
}



@media (max-width:768px){
    .homeSec{
        background: var(--clr-background);
