.projectList{
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-top: 2rem;
}
.projectItem{
    display: flex;
    flex-direction: row;
    position: relative;
    width: 100%;
    margin-bottom: 2rem;
    border-radius: .8rem;
    overflow: hidden;
    cursor: pointer;
    box-shadow: 0px 4px 8px #8809ff94;
}
.projectItemReverse{
    flex-direction: row-reverse;
}
/* .projectItem:nth-child(even){
    flex-direction: row-reverse;
} */
.projectItem::before{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    background: #b96fff1a;
